.modal__contModal{
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
   background-color: rgba(0, 0, 0, 0.644);
    display: flex;
    align-items: center;
    justify-content: center;
}
.contModal__father{
    width: 50%;
    height: auto;
    background-color: rgba(255, 255, 255, 0.63);
    border-radius: 10px;
}

.father__contButtons{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 15px;
    padding: 20px 0px 15px 0px;
}

.father__button{
    border: none;
    padding: 12px 20px;
    border-radius: 5px;
    font-weight: 600;
    transition: all 0.5s;
}
.father__button:hover{
    padding: 14px 30px;
    box-shadow: 16px 12px 12px 0px black;
    cursor: pointer;
}
.contButtons__cancelar{
    padding: 12px 25px;
}