*{
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: sans-serif;
}
body{
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image: url(../imgs/astronomia-cielo-nocturno-galactico-ciencia-combinada-ia-generativa.jpg);
  
}
h1{
  text-align: center;
  margin: 10px 0px;
  font-size: 40px;
  color: rgb(36, 36, 36);
}
h2{
  font-size: 16px;
  color: rgb(31, 31, 31);
  margin-top: 20px;
}

.content{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}
.content__cont{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px 25px;
  border-radius: 10px;
  background-color: rgba(235, 246, 255, 0.473);
}
.cont__TodoCounter{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.cont__TodoSearch{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.cont__TodoSearch input{
  padding: 10px 30px;
  border-radius: 10px;
  border: none;
  transition: all 0.5s;
}
.cont__TodoSearch input:hover{
  padding: 12px 32px;
  box-shadow: 16px 12px 12px 0px black;
  cursor: pointer;
}

.cont__TodoList ul{
  display: flex;
  flex-direction: column;
  gap: 20px;
  overflow: scroll;
  padding: 20px;
  height: 250px;
  width: 90%;
}
.cont__TodoList ul li{
  display: flex;
  justify-content: space-between;
  padding: 10px;
  gap: 20px;
  background-color: rgb(197, 197, 197);
  border-bottom-left-radius: 10px;
  border-top-right-radius: 10px;
  transition: all 0.5s;
  width: 330px;
}
.scroll-box {
  width: 300px; 
  height: 200px; 
  overflow-y: scroll; 
  overflow-x: hidden; 
  scrollbar-width: thin; 
  scrollbar-color: #888 transparent;
}

.scroll-box::-webkit-scrollbar {
  width: 6px; 
}

.scroll-box::-webkit-scrollbar-thumb {
  background-color: #1755ff; 
  border-radius: 5px; 
}

.scroll-box::-webkit-scrollbar-thumb:hover {
  background-color: #00aeff; 
}

.scroll-box::-webkit-scrollbar-track {
  background-color: transparent; 
}

.cont__TodoList ul li span{
  color: brown;
  font-size: 20px;
  font-weight: 900;
}
.cont__TodoList ul li span:hover{
  color: red;
}
.cont__TodoList ul li:hover{
  padding: 12px;
  box-shadow: 16px 12px 12px 0px black;
  cursor: pointer;
}

.li__contInfo{
  display: flex;
  gap: 10px;
}
img{
  width: 100%;
  height: 100%;
}

.cont__Button{
  position: relative;
  width: 100%;
}

.cont__Button button{
  background-color: rgb(33, 226, 226);
  border: none;
  padding: 10px 17px;
  border-radius: 100%;
  font-size: 25px;
  position: absolute;
  right: -42px;
  box-shadow: 0px 0px 20px 6px aqua;
  font-weight: 600;
  transition: all 0.5s;
}

.cont__Button button:hover{
  transform: rotate(315deg);
  cursor: pointer;
}



.li__contInfo{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  flex-direction: row;
}
.Icon--check{
  width: 25px;
  height: 25px;
  background-image: url(../imgs/icons8-v-key-48\ \(1\).png);
  background-size: cover;
}
.Icon--check--active{
  background-image: url(../imgs/icons8-v-key-48.png);
  background-size: cover;
}
.TodoItem-p--complete{
  text-decoration:line-through;
}


.veryWell__cont{
  background-color: #a0a0a093;
  padding: 10px;
  position: relative;
  bottom: 600px;
  animation-name: bajar;
  animation-iteration-count:unset;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}
@keyframes bajar{
  0%{

  }
  100%{
    bottom: 0;
  }
}
h3{
  color: rgb(255, 255, 255);
}

.solarSystem {
  background-image: url(../imgs/icons8-solar-system-47.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  padding: 30px;
  width: 20px;
  height: 30px;
  animation-name: girar;
  animation-duration: 5s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}

@keyframes girar {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}